.header {
    >.header-content {
        text-align: center;
        position: relative;
        background-repeat: repeat;
        background-image: url(../../../images/header-background.jpg);
        background-size: 70%;
        padding: var(--size-margin-30) 0;
        font-size: var(--size-font-size-header);
        >.header-recipes {
            font-family: PlayfairSC;
        }
        >.header-by-maki {
            font-family: Dawning;
            margin-top: calc(var(--size-margin-30) * (-1));
        }
    }
}

@media screen and (max-width: 600px) {
    .header {
        >.header-content {
            padding: var(--size-margin-10) 0;
            >.header-by-maki {
                margin-top: calc(var(--size-margin-10) * (-1));
            }
        }
    }
}